.App {
  text-align: center;
  padding: 0 100px;
  margin-bottom: 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#tokenPrice {
  font-weight: 500;
  text-align: left;
}
#swap-raca {
  font-weight: 500;
  text-align: left;
}
#swap-raca input {
  height: 20px;
}
#tablePrice {
  width: 60%;
  border-spacing: 10px;
}
.cards {
  display: flex;
  flex-flow: row wrap;
  margin-left: -12px;
  margin-right: -12px;
  row-gap: 0px;
  padding: 40px 0;
}
.card {
  padding-left: 12px;
  padding-right: 12px;
}
.nft {
  width: 286px;
  height: 456px;
  margin-bottom: 60px;
  border: 2px solid rgba(56, 56, 56, 0.1);
  cursor: pointer;
  position: relative;
  border-radius: 5px;
}
.imgBox {
  position: relative;
  width: 284px;
  height: 284px;
  border-radius: 0;
}
.textBox {
  padding: 16px 16px 0;
}
.image-nft {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.name-nft {
  color: #383838;
  font-weight: 700;
  font-size: 20px;
  line-height: 32px;
  white-space: nowrap;
}

/* .separate {
  display: flex;
  clear: both;
  width: 100%;
  min-width: 100%;
  margin: -12px 0;
} */
.priceNum {
  margin-left: 12px;
  color: #383838;
  font-weight: 700;
  font-size: 16px;
  font-family: PingFang SC;
  font-style: normal;
  line-height: 24px;
  font-feature-settings: "pnum" on, "lnum" on;
}
.price {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
#withdraw {
  width: 50%;
  height: 400px;
}
#addTransaction {
  text-align: left;
  display: flex;
  flex-direction: column;
  width: 50%;
  margin-bottom: 10px;
}
#toggleButton {
  width: 50%;
  margin-bottom: 10px;
  background-image: linear-gradient(to right, #4568dc, #b06ab3);
}

#addButton {
  background-image: linear-gradient(to right, #6190e8, #a7bfe8);
}
.MuiTabs-indicator.css-ttwr4n,
.css-1aquho2-MuiTabs-indicator {
  background-image: linear-gradient(to right, #4568dc, #b06ab3);
}
.css-1q2h7u5,
.css-1h9z7r5-MuiButtonBase-root-MuiTab-root {
  width: 50%;
}

.css-1h9z7r5-MuiButtonBase-root-MuiTab-root.Mui-selected,
.css-1q2h7u5.Mui-selected {
  /* background: -webkit-linear-gradient(90deg,#b06ab3, #6190e8); */
  font-weight: 800;
  /* -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; */
}
#tabs {
  width: 300px;
}
.sales {
  padding-top: 20px;
}
#timeUpdated {
  text-align: left;
}
#tablePrice th {
  font-weight: bold;
  padding: 0px;
  font-size: 1rem;
}
#tablePrice td {
  font-size: 1rem;
}

#table-scroll {
  width: 60%;
}
#tablePrice {
  width: 100%;
  overflow-x: auto;
  border-spacing: 8px;
}

.loading .image-nft,
.loading .name-nft,
.loading .price {
  position: relative;
  overflow: hidden;
}
.loading .image-nft::before,
.loading .name-nft::before,
.loading .price::before {
  position: absolute;
  background-color: #dddddd;
  content: "";
  width: 100%;
  height: 80%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.loading .image-nft::after,
.loading .name-nft::after,
.loading .price::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 80%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(to right, #dddddd, #ffffff 50%, #dddddd);
  animation: skeletonLoading 2s infinite alternate;
}
@keyframes skeletonLoading {
  from {
    left: -40%;
  }
  to {
    left: 40%;
  }
}
@media screen and (max-width: 880px) {
  .App {
    padding: 0 5px;
  }
  .nft {
    width: 100%;
    height: 80%;
    margin-bottom: 24px;
  }
  .cards {
    width: 100vw;
  }
  .card {
    width: 43%;
    height: 100%;
  }
  .imgBox {
    width: 100%;
    height: 175px;
  }
  .name-nft {
    font-size: 14px;
    margin: 0px;
  }
  .textBox {
    padding: 0px 16px;
  }

  #tablePrice td {
    word-wrap: break-word;
    border-collapse: collapse;
    padding: 8px 0;
  }
  #table-scroll {
    width: 100%;
  }
  .css-19kzrtu {
    padding: 24px 0px !important;
  }
  #sales-section table tbody th:first-child,
  #sales-section table thead th:first-child {
    position: sticky;
    left: 0;
    background-color: white;
    padding-right: 10px;
  }
  #withdraw {
    width: 100%;
  }
  #addTransaction {
    width: 100%;
  }

  #tabs {
    width: 100%;
    margin-left: 0px;
  }

  .css-1h9z7r5 {
    width: calc(100% / 2);
  }
  .sales {
    padding: 0px;
  }
}
